<template>
  <div class="register">
    <div class="top"></div>
    <div class="register-bg">
      <div class="logo"></div>
    </div>
    <div>
      <van-cell-group>
        <van-field label="用户名" placeholder="请输入用户名" left-icon="manager" type="text" v-model="userName.value" :error-message="userName.error" @input="veriUserPass(userName.value, 'validUsername', {key: 'userName', error: '4-8个(字母数字_)组合，必须字母开头'})"/>

        <van-field label="密码" placeholder="请输入密码" :error-message="password1.error" left-icon="lock" :right-icon="password1.icon" :type="password1.type" @click-right-icon="switching('password1')" @input="veriUserPass(password1.value, 'validPwd', {key: 'password1', error: '6-16个(字母数字_)组合，必须含有大小写字母'})" v-model="password1.value"/>

        <van-field label="密码" placeholder="请再次输入密码" :error-message="password2.error" left-icon="lock" :right-icon="password2.icon" :type="password2.type" @click-right-icon="switching('password2')" v-model="password2.value" 
        @input="validEqual(password1.value, password2.value)"/>
      </van-cell-group>
      <!-- 注册按钮 -->
      <div class="determine">
        <van-button class="register-btn" type="info" block :loading="isLoad" loading-text="加载中..." @click="straining" :disabled="!isProhibit">确定</van-button>
      </div>
      <div class="login"><router-link class="login-text" to="/login">已有账号？登录</router-link></div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.register {
  .top {
    height: 24px;
  }
  .register-bg {
    height: 200px;
    background: url("../assets/images/timg.jpg") no-repeat center center;
    background-size: 100% 200px;
    position: relative;
    .logo {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: #fff;
      box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.5);
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }

  .determine {
    margin: 0 15px;
    .register-btn {
      border-radius: 21px;
    }
  }
  .login{
    margin-top: 20px;
    text-align: center;
  }
  .login-text{
    color: #666;
  }
}
</style>
<script>
import {validForm} from '../tool/validForm'

import {createNamespacedHelpers} from 'vuex'
//基于registerModule的命名空间导入mapState、mapMutations 辅助函数
const {mapState, mapMutations} = createNamespacedHelpers('registerModule');
export default {
  name: "register",

  //计算属性
  computed: {
    ...mapState(['password1', 'password2', 'isLoad', 'isProhibit', 'userName'])
  },

  // 方法
  methods: {
    ...mapMutations(['switching']),

    straining() {

      let self = this;

      // 点击注册  
      this.$store.commit('registerModule/straining', true);

      let randomTime = Math.random() * 5 * 1000;

      setTimeout(function () {
        // 获取所有用户信息
        let users = localStorage.getItem('users');
        users = users ? JSON.parse(users) : [];
        // console.log('users :', users);

        //判断当前用户是否存在
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == self.userName.value) {
            self.$toast({
              duration: 2000,
              message: '该用户已存在'
            });

            self.$store.commit('registerModule/straining', false);
            return;
          }
        }

        self.$toast({
          duration: 2000,
          message: '注册成功'
        })
        self.$store.commit('registerModule/straining', false);
        
        let time = new Date().getTime();
        // 记录用户信息
        let recordTime = {
          // 用户名
          username: self.userName.value,
          // 密码
          password: self.password1.value,

          // 注册时间
          time
        };

        //将当前用户信息推进users
        users.push(recordTime);

        //写入本地存储,模拟数据库
        localStorage.setItem('users', JSON.stringify(users));

        //跳转到登录页面
        self.$router.push({name: 'login'});
      }, randomTime)
      
    },
  

    // 验证用户名
    validUsername(value) {
      // console.log('value :', value)

      let isProhibit = validForm.validUsername(value);
      this.$store.commit('registerModule/validUsername', isProhibit)
    },

    // 验证密码
    validPwd(value) {
      let isProhibit = validForm.validPwd(value);
      this.$store.commit('registerModule/validPwd', isProhibit)
    },
    // 验证用户名和密码
    veriUserPass(value, fnName, o) {
      let isProhibit = validForm[fnName](value);
      o.isProhibit = isProhibit;
      this.$store.commit('registerModule/veriUserPass', o);

      this.$store.commit('registerModule/adopt')
    },
    // 验证两个密码是否相等
    validEqual(v1, v2) {
      let isProhibit = validForm.validEqual(v1, v2);
      this.$store.commit('registerModule/validEqual', isProhibit)

      this.$store.commit('registerModule/adopt')
    }
  },

  
};
</script>
 